/* Base Banner Styles */
.banner {
  position: relative;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #1551f6;
    overflow: hidden;
    margin: 0;
    padding: 0;
    transition: opacity 0.3s ease-out, transform 0.1s ease-out;
}

.hidden {
  opacity: 0;
  transform: scale(1);
  pointer-events: none;
}

.scrolling-text-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; /* Keep this for scrolling */
  height: 30%;
  transform: translateY(-50%);
  z-index: 1;
  animation: scroll-text 30s linear infinite;
}

@keyframes scroll-text {
  from {
    transform: translateX(0) translateY(-50%);
  }
  to {
    transform: translateX(-50%) translateY(-50%);
  }
}

.static-image-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  transition: transform 0.1s ease-out;
}

.static-image-container img {
  width: auto;
  max-width: 100%;
  height: auto;
  object-fit: contain; /* Keep aspect ratio */
}

/* Show BannerMobile on mobile screens (max-width: 767px) */
@media (max-width: 767px) {
  .Banner {
    display: none;
  }

  .Banner {
    display: block;
  }
}
