.catch-section-mobile {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-image: url('./background.jpeg'); /* Add your background image */
    background-size: cover; /* Make sure the background covers the entire section */
    background-position: center; /* Center the background */
}

.catch-me-container-mobile {
    position: absolute;
    cursor: pointer;
    transition: top 0.5s cubic-bezier(0.4, 0, 0.2, 1), left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    will-change: top, left;
}

.catch-box {
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative; 
    transition: transform 0.3s ease;
}

.catch-image {
    width: 200px; /* Adjust size as needed */
    height: auto; 
}

.hover-text {
    position: absolute;
    bottom: -30px;
    color: #111111;
    font-size: 1.5rem;
    font-weight: bold;
}

.mb-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.mb-modal-content {
    background-color: #0047ab; /* Superman blue */
    color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 80%; /* Responsive width */
    max-width: 400px; /* Limit max width */
}

.profile-pic {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 10px auto;
    display: block;
}

.close {
    cursor: pointer;
    font-size: 1.5rem;
}

.contact-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.icon {
    font-size: 2rem;
    color: #e31937; /* WhatsApp color */
}


.catch-image.fly {
    animation: flyEffect 1s ease-in-out infinite; /* Activate fly animation */
}

@keyframes flyEffect {
    0% {
        transform: scale(1) rotate(0deg);
    }
    50% {
        transform: scale(1.1) rotate(10deg);
    }
    100% {
        transform: scale(1) rotate(0deg);
    }
}