.archive-container {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow: hidden;
  border: 20px solid rgb(255, 255, 255) ;


}

.projects-list {
  flex: 1;
  max-width: 900px;
  background: white; /* خلفية نصف شفافة داكنة */
  backdrop-filter: blur(10px); /* تطبيق تأثير التمويه */
  color: black;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
}

.project-item {
  margin-bottom: 15px;
  padding: 10px;
  cursor: pointer;
  border-radius: 8px;
}

.project-item.active {
  background-color: black;
  color: white;
}

.project-preview {
  flex: 3;
  background-size: cover; /* Ensure the image fits inside without cutting */
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 10px;
  border-radius: 20px;
  max-width: 800px; /* Limit the width of the preview box */
  max-height: 800px; /* Limit the height of the preview box */
  
}


.contact-button {
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 8px 16px; /* Reduce padding to match smaller box */
  border: none;
  cursor: pointer;
  font-size: 14px;
  border-radius: 8px;
  position: absolute;
  bottom: 20px; /* Adjust position */
}

.secure-icon {
  margin-right: 10px; /* مساحة بين الأيقونة والنص */
}

/* Hide desktop archive on screens smaller than 768px */
@media (max-width: 768px) {
  .archive-container {
    display: none;
  }
}
