.catch-section {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-image: url('./background.jpeg');
    background-size: cover;
    background-position: center;
    border: 20px solid white;
}

.catch-me-container {
    position: absolute;
    cursor: pointer;
    transition: transform 0.5s ease-in-out, top 0.5s ease-in-out, left 0.5s ease-in-out;
    will-change: transform, top, left;
}

.catch-me-container:hover {
    transform: scale(1.2) rotate(5deg); /* Slight tilt when hovering */
}

.catch-box {
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.catch-image {
    width: 260px;
    height: auto;
    transition: transform 0.5s ease;
}

.catch-image.fly {
    animation: flyEffect 1s ease-in-out infinite; /* Activate fly animation */
}

@keyframes flyEffect {
    0% { transform: scale(1) rotate(0deg); }
    50% { transform: scale(1.1) rotate(10deg); }
    100% { transform: scale(1) rotate(0deg); }
}

.hover-text {
    position: absolute;
    bottom: -30px;
    color: #111111;
    font-size: 1.5rem;
    font-weight: bold;
}

/* Modal and Superman theme */
.su-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.su-modal-content {
    background-color:#0047ab;
    padding: 100px;
    border-radius: 10px;
    text-align: center;
}

.su-superman-theme {
    background-color: #0047ab; /* Superman blue */
    color: white; /* Superman yellow */
    border: 2px solid #e31937; /* Superman red */
}

.close {
    cursor: pointer;
    font-size: 1.5rem;
}

.profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 10px auto;
    display: block;
}

.contact-info {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
}

.icon {
    font-size: 2rem;
    margin-right: 10px;
    color:  #e31937; /* WhatsApp green */
}