.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./lod.jpg'); /* Add your background image */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #ffffff; /* White text */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
}

.loading-text {
  font-size: 2.5rem;
  animation: fadeInOut 3s ease-in-out infinite, softPulse 4s ease-in-out infinite;
  text-shadow: 0 0 10px #ff0000; /* Soft red glow */
}

@keyframes fadeInOut {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes softPulse {
  0% {
    transform: scale(1);
    text-shadow: 0 0 10px #ff0000;
  }
  50% {
    transform: scale(1.05);
    text-shadow: 0 0 20px #ff0000;
  }
  100% {
    transform: scale(1);
    text-shadow: 0 0 10px #ff0000;
  }
}

.loading-subtext {
  font-size: 1.2rem;
  color: #ff0000; /* Red subtext */
  margin-top: 1rem;
  animation: fadeInOut 3s ease-in-out infinite;
}

.soft-bg-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 1));
  animation: softGlow 6s ease-in-out infinite;
  z-index: -1;
}

@keyframes softGlow {
  0%, 100% {
    opacity: 1;
    filter: blur(0px);
  }
  50% {
    opacity: 0.8;
    filter: blur(3px);
  }
}
