.footer-container {
  background-color: #fff;
  padding: 20px; /* Add padding for spacing */
  text-align: center;
  border-top: 1px solid #e5e5e5;
  font-family: 'Arial', sans-serif;
  position: relative; /* Ensure proper positioning */
  z-index: 1; /* Keep it above other elements */
}

.footer-logo {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.footer-logo-image {
  width: 800px; /* Adjust this value to make the logo smaller */
  height: auto; /* Keep aspect ratio */
  max-width: 100%; /* Ensures responsiveness */
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.social-icon {
  width: 90px; /* Adjust the size as needed */
  height: 90px; /* Adjust the size as needed */
  border-radius: 50%; /* Make the image circular */
  margin: 0 10px; /* Space between icons */
}

.footer-info {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 20px;
}

.footer-webflow-logo {
  background-color: #2962FF;
  color: white;
  padding: 2px 5px;
  border-radius: 3px;
  display: inline-block;
}

.footer-time {
  display: block;
  font-size: 2rem;
  font-weight: bold;
  color: #333;
  margin-top: 10px;
}

.footer-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 30px;
}

.footer-button {
  font-size: 1rem;
  color: #333;
  padding: 15px;
  border: 1px solid #333;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.footer-button:hover {
  color: #0073e6;
  border-color: #0073e6;
}

.footer-button:after {
  content: "↗";
  margin-left: 5px;
}

.footer-copyright {
  margin-top: 40px;
  font-size: 0.875rem;
  color: #666;
}
