
.banner-mobile {
    position: relative;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #1551f6;
    overflow: hidden;
    margin: 0;
    padding: 0;
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
  }
  
  .hidden {
    opacity: 0;
    transform: scale(1);
    pointer-events: none;
  }
  
  .scrolling-text-container {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 30%;
    transform: translateY(-20%);
    z-index: 1;
    animation: scroll-text 6s linear infinite;
  }
  
  @keyframes scroll-text {
    from {
      transform: translateX(0) translateY(-50%);
    }
    to {
      transform: translateX(-50%) translateY(-50%);
    }
  }
  
  .static-image-container {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    transition: transform 0.1s ease-out;
  }
  
  .static-image-container img {
    width: 100vw;
    height: 100vw;
   
  }
  
